@import './loading.css';
ul,li{
    list-style: none;
}
.container .nav-left{
    height:calc(100vh) ;
    background-color: #001529;
}
.container .main{
    height: calc(100vh);
    overflow: auto;
    background-color: #f1f3f5;
}
.container .content{
    position: relative;
    padding: 20px;
}
.content-wrap{
    background-color: #fff;
    border:  1px solid #e8e8e8;
    margin-top: -3px;
}

.clearfix::after {
    content: ' ';
    clear: both;
    display: block;
    visibility: hidden;
  }
  